import { Component, Inject, OnInit } from "@angular/core";
import { MatDialogRef, MAT_DIALOG_DATA } from "@angular/material";
import { DialogData } from "../user/user.component";
import * as _ from 'lodash';
import { take } from "rxjs/operators";
import { AuthService } from '@frontend/common/auth/shared';
import { UserService } from '@frontend/common/user';

@Component({
  selector: 'user-dialog',
  templateUrl: './user-dialog.component.html',
  styleUrls: ['./user-dialog.component.scss']
})
export class UserDialog implements OnInit {
  mode: number;
  formData = {
    userId: null,
    password: null,
    email: null,
    type: null,
    nickname: null
  }
  title = {
    0: '新增',
    1: '編輯'
  }

  constructor(
    public dialogRef: MatDialogRef<UserDialog>,
    private authService: AuthService,
    public userService: UserService,
    @Inject(MAT_DIALOG_DATA) public data: DialogData
  ) {
    console.log(data)

    this.mode = data.mode;
    this.loadFormData(data);
  }

  ngOnInit() {
  }

  onNoClick(): void {
    this.dialogRef.close();
  }

  loadFormData(data: any) {
    if (!!data.formData) {
      _.assign(this.formData, data.formData);
    }
  }

  send() {
    if (this.mode === 0) {

      this.authService.register(this.formData).pipe(take(1)).subscribe(
        result => {

          if (!!result['status']) {
            this.dialogRef.close(true);

          }
        }
      )
    }

    if (this.mode === 1) {

      this.userService.updataUser(this.formData).pipe(take(1)).subscribe(
        result => {


            this.dialogRef.close(true);

        }
      )
    }
  }
}
